@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
}

.app-eader {
  height: auto;
}

.app-content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80%;
}

.navbar {
  /* position: fixed !important; */
  width: 100vw;
  /* margin-top: 1vh; */
  /* height: 150px; */
  /* background-image: linear-gradient(to right, #040404, #151616); */
}

.logo {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 40px !important;
  margin-left: 3%;
  color: black !important;
}

.logo:hover {
  color: black !important;
}

.header-logo {
  margin-left: 10px;
  width: 40px;
  margin-bottom: 5px;
}

.toggle {
  margin-right: 50px;
}

.navcontent {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  /* font-family: "Bebas Neue", sans-serif; */
  display: flex;
  justify-content: space-between;
  margin-right: 3%;
  /* width: 30%; */
  font-size: 13px;
}

.NavLink {
  color: black !important;
  /* padding-bottom: 1px !important; */
}

.NavLink:hover {
  padding-bottom: 1px !important;
  border-bottom: 2px solid #85ca77;
}

.NavLink:focus {
  padding-bottom: 1px !important;
  border-bottom: 2px solid #85ca77;
}

.ResumeLink {
  color: #6b6b6b !important;
  /* padding-bottom: 1px !important; */
}

.ResumeLink:hover {
  padding-bottom: 1px !important;
  border-bottom: 2px solid #85ca77;
}

.Home {
  width: 100%;
  height: 100%;
}

.main-paragraph {
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.intro-content {
  text-align: center;
  width: 70%;
  color: #6b6b6b;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  font-size: 40px;
}

.home-logo {
  width: 17%;
}

.About {
  width: 100%;
  height: 100%;
}

.about-container {
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* margin-top: 5%; */
}

.about-pivot-container {
  margin-top: 5%;
  width: 70vw;
  display: flex;
  flex-direction: column;
  /* flex-wrap: wrap; */
  align-items: center;
}

.team-container {
  margin-top: 5%;
  margin-bottom: 5%;
  width: 70vw;
}

.about-title {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 35px;
  width: 100%;
  color: black;
}

.horizontal-line {
  margin: -2px 0px 0px 0px;
  border: 1px solid black;
  width: 100%;
}

.about-pivot-content {
  display: flex;
  justify-content: center;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 15px;
  margin-top: 2%;
}

.about-buttons {
  margin-top: 2%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.repo-button {
  width: 230px;
  height: 70px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 20px;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid black;
  border-radius: 7px;
  transition: 0.3s;
  margin: 5px;
  text-decoration: none;
}

.repo-button:hover {
  background-color: #3f3f3f;
  color: white;
  width: 230px;
  height: 70px;
  font-size: 20px;
  border: 2px solid black;
  border-radius: 7px;
  text-decoration: none;
}

.team-content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.member-card {
  margin: 40px 10px 0px 10px !important;
}

.about-card-content {
  display: flex;
  justify-content: space-between;
}

.about-card-content::after {
  content: none !important;
}

.Code {
  width: 100%;
  height: 100%;
}

.code-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.code-header {
  margin-top: 5%;
  width: 70%;
}

.code-main-title {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 35px;
  width: 100%;
  color: black;
}

.code-box {
  width: 100% !important;
  box-shadow: none !important;
}

.code-content {
  display: flex;
  flex-direction: column;
  align-content: space-around;
}

.snippet-title {
  font-family: "Montserrat", sans-serif !important;
  font-size: 25px !important;
  padding-bottom: 10px;
}

.snippet-code pre {
  font-family: "Source Code Pro", sans-serif;
  font-weight: 400;
  font-size: 15px;
  margin-top: -10px;
  margin-bottom: 0px;
}

.code-list {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-top: 1%;
}

.snippet-container {
  width: 50%;
  min-width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

